<template>
  <div>
    <el-button type="primary" @click="$router.push('/goods/add')">添加<i class="el-icon-circle-plus-outline"></i></el-button>
    <div class="table-bg">
      <el-table :data="list" style="width: 100%;margin-bottom: 20px;" >
        <el-table-column label="商品ID" prop="id"></el-table-column>
        <el-table-column label="商品名称" prop="goodsname"></el-table-column>
        <el-table-column label="一级分类" prop="firstcatename"></el-table-column>
        <el-table-column label="二级分类" prop="secondcatename"></el-table-column>
        <el-table-column label="分类图片">
           <template slot-scope="scope">
               <img v-if="scope.row.img" :src="scope.row.img | imgurl" style="width:80px;" alt=""/>
              <span v-else>暂无！</span>
            </template>
        </el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
              <el-tag v-if="scope.row.status==1" type="success">正常</el-tag>
              <el-tag  v-else type="info">禁用</el-tag>
            </template>
        </el-table-column>
        <el-table-column label="操作" prop="status">
            <template slot-scope="scope">
              <el-button @click="$router.push('/goods/edit/'+scope.row.id)"  icon="el-icon-edit"  type="success" circle size="small"></el-button>
              <el-button @click="del(scope.row.id)" type="danger" icon="el-icon-delete" circle size="small"></el-button>
            </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { getGoodsList ,delGoods} from "@/request/goods"
export default {
  data() {
    return {
      page:1,
      size:100,
      list:[]
    };
  },
  mounted() {
      this.getlist();
  },
  methods: {
    async getlist(){
       this.list = await getGoodsList(this.page,this.size);
    },
    del(id){
        this.$confirm('确定删除吗？', '提示', {
            confirmButtonText: '删他！',
            cancelButtonText: '不要！',
            type: 'warning'
        }).then(async () => {
            await delGoods(id);
            this.getlist();
        })
     }
  },
  
};
</script>
<style scoped>
</style>


